.stroke-a {
  position: relative;
}

.stroke-a svg {
  position: absolute;
  top: 50%;
  left: 50%;
  pointer-events: none;
  display: block;
  transform: translate(-50%, -50%);
}

.stroke-a svg path {
  transition: stroke-dashoffset 1s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.stroke-a svg path.stroked {
  stroke-dashoffset: 0px !important;
}

.stroke-a a {
