.input-wrapper label::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -1px;
  height: 100%;
  width: 100%;
  border-bottom: 1px solid;
  border-color: #131217;
  transition: opacity .3s ease;
  align-self: center;
  justify-self: center;
