  text-transform: uppercase;
  left: 0;
  opacity: 1;
  transition: color 0.5s ease, transform 0.5s ease, font-size 0.5s ease;
}

.focus + .label .label-content {
  transform: translateY(-35px);
  font-size: 16px;
  opacity: 1;
  color: #9c27b0;
}

.focus + .label:after {
  opacity: 1;
}

