.video-player__small-play-button {
  cursor: pointer;
  height: 16px;
  position: relative;
  width: 20px;
}

.video-player__small-play-button svg {
  height: 100%;
  width: 100%;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: white;
  transition: fill .2s ease;
}

.video-player__small-play-button svg.active {
  display: block;
}

.video-player__small-play-button:hover svg {
  fill: #9c27b0;
}

.video-player__controls {
  position: absolute;
  bottom: 0px;
  padding-bottom: 20px;
  transform: translateX(-50%);
  left: 50%;
  padding-left: 4.9%;
  padding-right: 4.9%;
  width: 100%;
  opacity: 0;
}

.video-player__controls.active {
  opacity: 1;
}

.video-player__controls-top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  width: 100%;
  align-items: center;
}

.video-player__progress-bar-container {
  background: rgba(255, 255, 255, 0.7);
  width: 100%;
  height: 4px;
  cursor: pointer;
  position: relative;
}

.video-player__progress-bar {
  height: 100%;
  background: #9c27b0;
  width: 0px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
}

.video-player__buffer-bar {
  height: 100%;
  background: rgba(156, 39, 176, 0.5);
  width: 0px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
}

.video-player__full-screen {
  width: 18px;
  height: 18px;
  cursor: pointer;
  margin-left: 41px;
}

.video-player__full-screen svg {
  pointer-events: none;
  height: 100%;
  fill: white;
  transition: fill .2s ease;
}

.video-player__full-screen:hover svg {
  fill: #9c27b0;
}

.video-player__time {
  font-size: 14px;
  color: white;
  line-height: 1em;
  margin-left: 41px;
  font-family: 'GTF-Regular', sans-serif;
  font-weight: 400;
  display: flex;
  align-items: center;
}

.video-player__controls-left {
  display: flex;
  align-items: center;
}

.video-player__controls-right {
  display: flex;
  align-items: baseline;
}

.video-player__sound {
  cursor: pointer;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.video-player__sound svg {
  fill: white;
  transition: fill .2s ease;
  height: 20px;
}

.video-player__sound svg:hover {
  fill: #9c27b0;
}

.video-player__sound:hover .video-player__sound-progress-wrapper {
  width: 66px;
  overflow: initial;
}

.video-player__sound-progress-wrapper {
  background: rgba(255, 255, 255, 0.7);
  height: 4px;
  width: 0px;
  overflow: hidden;
  cursor: pointer;
  position: relative;
  margin-left: 10px;
  transition: width .3s ease;
}

.video-player__sound-bar {
  height: 100%;
  background: #9c27b0;
  width: 0px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
}

@media (max-width: 460px) {
  .video-player {
    width: 85.6vw;
  }
  .video-player__play-button {
    width: 60px;
    height: 60px;
    font-size: 12px;
  }
  .video-player__controls {
    padding-bottom: 8px;
  }
  .video-player__controls-top {
    margin-bottom: 10px;
  }
  .video-player__time {
    margin-left: 11px;
  }
}

#app {
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  touch-action: none;
  width: 100%;
  pointer-events: none;
  background-color: transparent;
}

img {
  max-width: 100%;
}

.js-webgl-element-hidden {
  opacity: 0;
}

.gui {
  position: fixed;
  top: 3%;
  right: 2%;
}

a {
  text-decoration: none;
  color: inherit;
}

header h1 {
  font-size: 6.1vw;
  text-transform: uppercase;
  max-width: 1494px;
  line-height: 1.17em;
  width: 77.8vw;
  font-family: 'HelveticaMedium', sans-serif;
  font-weight: 500;
}

header h1 span {
  display: inline-block;
  line-height: 1em;
  opacity: 0;
  transform: translateY(140px);
}

header h1 .h1-decor {
  font-family: 'GTF-bold', sans-serif;
  font-weight: 400;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
  color: transparent;
  cursor: pointer;
}

header h1 .h1-decor::after {
  content: '';
  display: block;
  width: 100%;
  height: 5px;
  background: white;
  transition: width .4s ease;
}

header h1 .h1-decor:hover::after {
  width: 0%;
}

header h1 .h1-decor a {
  color: inherit;
  -webkit-text-stroke-color: white;
}

header h1 .website {
  padding-left: 135px;
}

header h1 .for-business {
  padding-left: 72px;
}

h2 {
  font-size: 6.1vw;
  text-transform: uppercase;
  line-height: 1.17em;
  opacity: 0;
  transform: translateY(20px);
  font-family: 'HelveticaMedium', sans-serif;
  font-weight: 500;
}

h3 {
  font-size: 3.1vw;
  font-family: 'HelveticaMedium', sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  opacity: 0;
  transform: translateY(20px);
}

h4 {
  opacity: 0;
  transform: translateY(60px);
}

header {
  width: 100%;
  height: 100vh;
  position: relative;
}

.site-wrapper {
  overflow: hidden;
}

.container {
  width: 100%;
  height: 100%;
  padding-left: 4vw;
  padding-right: 6.8vw;
}

.container.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.container.heading-container {
  padding-left: 4vw;
  padding-right: 0px;
}

.container.awards-container {
  padding-right: 0px;
  display: inline-block;
}

.indent-top {
  padding-top: 20.3vh;
}

.indent-bottom {
  padding-bottom: 20.3vh;
}

footer {
  position: relative;
}

footer .container {
  display: flex;
}

footer .footer-left {
  margin-right: 11vw;
  width: 25%;
  position: relative;
  z-index: 10;
}

footer .footer-right {
  width: 75%;
  position: relative;
}

footer .button {
  margin-top: 23.2vh;
  opacity: 0;
}

footer .contact-me {
  line-height: 1em;
  font-size: 1.2vw;
  opacity: 0;
  font-family: 'GTF-Regular', sans-serif;
  font-weight: 400;
}

.contacts-wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 14vh;
}

.contacts-wrapper h3 {
  margin-top: 30px;
  line-height: 1em;
}

.contacts-line {
  width: 0%;
  height: 1px;
  background: white;
}

.contacts--left, .contacts--right {
  width: 40%;
}

.contacts .stroke-a {
  width: fit-content;
}

.contacts a {
  display: block;
  text-transform: uppercase;
  font-size: 1.4vw;
  margin-top: 44px;
  width: fit-content;
}

.contacts .footer-animate {
  opacity: 0;
  transform: translateY(20px);
}

